import React from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import InfiniteScrollCard from "./InfiniteScrollCard";

function InfiniteMovieList({
  movies,
  infiniteScrollData,
}) {
  const [changePosition, setChangePosition] = React.useState(false);

  const infiniteScrollStyle = {
    display: "flex",
    maxWidth: "1050px",
    margin: "0 auto",
    flexWrap: "wrap",
  };
  console.log(movies);
  return (
    <div>
      <button
        onClick={() => setChangePosition(bool => !bool)}
        className="changeCardPosition"
      >
        Change cards position
      </button>
      <div>
        <InfiniteScroll
          style={infiniteScrollStyle}
          dataLength={movies.length}
          next={() => infiniteScrollData()}
          scrollThreshold={1}
          hasMore={true}
        >
          {movies.map((element, index) => (
            <InfiniteScrollCard
              changePosition={changePosition}
              title={element.title}
              key={index}
              img={element.poster_path}
              description={element.overview}
              movies={movies}
              id={element.id}
            />
          ))}
        </InfiniteScroll>
      </div>
    </div>
  );
}

export default InfiniteMovieList;
