import React from "react";
import { GoogleLogin } from "react-google-login";
import { GoogleLogout } from "react-google-login";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import { debounce } from "lodash";
import { getMoviesList, getPopularMovies, getGenres } from "./api/api";
import "./App.css";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Added } from "./components/Added";
import { CardInfo } from "./components/CardInfo";
import { Header } from "./components/Header";
import { MovieList } from "./components/MovieList";
import { GlobalContext } from "./context/state";
import AppReducer from "../src/context/AppReducer";
import UserInfo from "./components/UserInfo";
import InfiniteMovieList from "./components/InfiniteMovieList";

const initialState = {
  movieList: localStorage.getItem("movieList")
    ? JSON.parse(localStorage.getItem("movieList"))
    : [],
    comments: []
};

toast.configure();

const App = () => {
  const [movies, setMovies] = React.useState([]);
  const [totalPages, setTotalPages] = React.useState(null);
  const [pageNumber, setPageNumber] = React.useState(1);
  const [query, setQuery] = React.useState("");
  const [genre, setGenre] = React.useState([]);
  const [userName, setUserName] = React.useState("");
  const [userAvatar, setUserAvatar] = React.useState("");
  const [isSignedIn, setIsSignedIn] = React.useState(false);
  const [
    changeCardsPositionState,
    setChangeCardsPositionState,
  ] = React.useState(false);
  const [state, dispatch] = React.useReducer(AppReducer, initialState);

  React.useEffect(() => {
    localStorage.setItem("movieList", JSON.stringify(state.movieList));
  }, [state]);

  const addMovieToList = (movie) => {
    dispatch({ type: "ADD_MOVIE_TO_LIST", payload: movie });
    toast.success("The movie is added", {
      autoClose: 2000,
    });
  };

  const removeMovie = (id) => {
    dispatch({ type: "REMOVE_MOVIE", payload: id });
    toast.error("The movie is removed", {
      autoClose: 2000,
    });
  };

  const addComment = (comment) => {
    dispatch({type:"ADD_COMMENT",comment})
  }


  React.useEffect(() => {
    getPopularMovies().then((data) => {
      setMovies(data.results);
      setTotalPages(data.total_pages);
      console.log(data);
    });
    getGenres().then((data) => {
      setGenre(
        Object.fromEntries(data.genres.map(({ id, name }) => [id, name]))
      );
    });
  }, []);

  const debounceUpdateMovies = React.useMemo(
    () =>
      debounce((query, pageNumber) => {
        if (query.length > 0) {
          getMoviesList({ query, pageNumber }).then((data) => {
            setMovies(data.results);
            setTotalPages(data.total_pages);
          });
        } else {
          getPopularMovies({ pageNumber }).then((data) => {
            setMovies(data.results);
            setTotalPages(data.total_pages);
          });
        }
      }, 300),
    []
  );

  const onChange = (e) => {
    e.preventDefault();
    debounceUpdateMovies(e.target.value, pageNumber);
    setQuery(e.target.value);
  };

  const nextPage = () => {
    if (query.length > 0 && pageNumber < totalPages) {
      setPageNumber(pageNumber + 1);
      getMoviesList({ page: pageNumber + 1, query: query }).then((data) => {
        setMovies(data.results);
        setTotalPages(data.total_pages);
      });
    } else if (query.length === 0 && pageNumber < totalPages) {
      setPageNumber(pageNumber + 1);
      getPopularMovies({ page: pageNumber + 1, query: query }).then((data) => {
        setMovies(data.results);
        setTotalPages(data.total_pages);
      });
    }
  };

  const infiniteScrollData = () => {
    console.log("hello world");
    setPageNumber(pageNumber + 1);
    getPopularMovies({ page: pageNumber, query: query }).then((data) => {
      setMovies([...movies, ...data.results]);
      setTotalPages(data.total_pages);
    });
  };

  const prevPage = () => {
    if (query.length > 0 && pageNumber > 1) {
      setPageNumber(pageNumber - 1);
      getMoviesList({ page: pageNumber - 1, query: query }).then((data) => {
        setMovies(data.results);
        setTotalPages(data.total_pages);
      });
    } else if (query.length === 0 && pageNumber > 1) {
      setPageNumber(pageNumber - 1);
      getPopularMovies({ page: pageNumber - 1, query: query }).then((data) => {
        setMovies(data.results);
        setTotalPages(data.total_pages);
      });
    }
  };

  const responseGoogle = (response) => {
    console.log(response);
    setUserName(response.profileObj.name);
    setUserAvatar(response.profileObj.imageUrl);
    setIsSignedIn(true);
  };

  const logout = () => {
    setUserName("");
    setUserAvatar("");
    setIsSignedIn(false);
  };

  const movieIsAdded = () => {};

  return (
    <GlobalContext.Provider
      value={{
        movieList: state.movieList,
        added: state.added,
        comments: state.comments,
        addComment,
        addMovieToList,
        removeMovie,
      }}
    >
      <div style={{ display: "flex", justifyContent: "space-around" }}>
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            width: "15%",
          }}
        >
          <GoogleLogin
            clientId={
              "207215828927-45a8gop4ba2c0hcm0vhrbndrkvqcfs6t.apps.googleusercontent.com"
            }
            buttonText={userName ? "Signed in" : "Sign in"}
            onSuccess={responseGoogle}
            onFailure={responseGoogle}
            cookiePolicy={"single_host_origin"}
            isSignedIn={true}
          />
          <GoogleLogout
            clientId={
              "207215828927-45a8gop4ba2c0hcm0vhrbndrkvqcfs6t.apps.googleusercontent.com"
            }
            buttonText="Logout"
            onLogoutSuccess={logout}
            isSignedIn={false}
          />
        </div>
        {isSignedIn && <UserInfo img={userAvatar} name={userName} />}
      </div>
      <Router>
        <Header />
        <Switch>
          <Route exact path="/">
            <MovieList
              setChangeCardsPositionState={setChangeCardsPositionState}
              movies={movies}
              genre={genre}
              prevPage={prevPage}
              nextPage={nextPage}
              query={query}
              onChange={onChange}
              movieIsAdded={movieIsAdded}
              changeCardsPositionState={changeCardsPositionState}
            />
          </Route>
          <Route path="/added">
            <Added genre={genre} />
          </Route>
          <Route path="/info/:id">
            <CardInfo movies={movies} genre={genre} />
          </Route>
          <Route path="/infiniteScroll">
            <InfiniteMovieList
              movies={movies}
              totalPages={totalPages}
              pageNumber={pageNumber}
              infiniteScrollData={infiniteScrollData}
            />
          </Route>
        </Switch>
      </Router>
    </GlobalContext.Provider>
  );
};

export default App;
