import React from "react";

const ControlPanel = ({ query, onChange, nextPage, prevPage,setChangeCardsPositionState }) => {
  return (
    <>
    <button onClick={() => setChangeCardsPositionState(bool => !bool)} className="changeCardPosition">Change cards position</button>
    <div className="controlPanel">
      <button onClick={prevPage}>Prev Page</button>
      <input
        placeholder="  Search..."
        value={query}
        type="text"
        onChange={onChange}
      />
      <button onClick={nextPage}>Next Page</button>
    </div>
    
    </>
  );
};

export default ControlPanel;