import React from "react";
import { Card } from "./Card";
import ControlPanel from "./ControlPanel";
import HorizontalCard from "./HorizontalCard";

export const MovieList = ({
  movies,
  genre,
  query,
  onChange,
  nextPage,
  prevPage,
  movieIsAdded,
  setChangeCardsPositionState,
  changeCardsPositionState,
}) => {
  return (
    <>
      <ControlPanel
        setChangeCardsPositionState={setChangeCardsPositionState}
        prevPage={prevPage}
        nextPage={nextPage}
        query={query}
        onChange={onChange}
      />
      <div className="wrapper">
        {movies.map((elem, index) => {
          return (
            <React.Fragment key={index}>
              {changeCardsPositionState ? (
                <div style={{height:"100%"}}>
                <HorizontalCard key={index} movies={elem} genre={genre} />
                </div>
              ) : (
                <Card
                  description={elem.overview}
                  movieIsAdded={movieIsAdded}
                  id={elem.id}
                  key={elem.id}
                  title={elem.title}
                  img={elem.poster_path}
                  genre={genre}
                  movies={elem}
                  genre_ids={elem.genre_ids}
                />
              )}
            </React.Fragment>
          );
        })}
      </div>
    </>
  );
};
